.rowstyle {
  margin-left: 1%;
}

.skin-blue .panel.panel-default.referinfo {
  border: 1px solid #d9dee2;
}

.skin-blue .table tbody tr td {
  padding: 13px 4px;
}

.fileinput-button {
  position: relative;
  overflow: hidden;
  height: 33px;
  display: inline-block;
}

.fileinput-button input {
  position: absolute;
  right: 0px;
  top: 0px;
  width: 100%;
  opacity: 0;
  -ms-filter: 'alpha(opacity=0)';
}

.skin-blue .checkbox {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0px;
}

.skin-blue label {
  display: inline-block;
  max-width: 100%;
  margin-bottom: 0px;
  /* font-weight: 700 !important; */
}

.skin-blue .radio {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0px;
}

.skin-blue .checkbox>label {
  position: relative;
  cursor: pointer;
  margin-right: 10px;
  padding-left: 25px;
  margin-bottom: 15px;
}

.skin-blue .radio>label {
  margin-bottom: 15px;
  position: relative;
  padding-left: 25px;
  margin-right: 10px;
  color: #666666;
  width: 20px;
}

.el-input-number--mini {
  width: 80px;
  line-height: 26px;
}
